<template>
  <div>
    <div v-for="(find_info,index) in find_infos" :key="index" class="infinite-list-item">
      <el-card class="left">
        <div slot="header" class="clearfix">
          <div>名称：{{find_info.name}}</div>
          <div>类型：{{find_info.category}}</div>
          <div>描述：{{find_info.discrip}}</div>
          <div>拾取时间：{{find_info.time}}</div>
          <div>联系方式：{{find_info.contact_info}}</div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
  export default {
    name: "findinfo",
    data() {
      return {
        find_infos: [{
            id: ''
          },
          {
            name: ''
          },
          {
            discrip: ''
          },
          {
            category: ''
          },
          {
            place: [
              '',
              ''
            ]
          },
          {
            time: ''
          },
          {
            user_name: ''
          },
          {
            contact_info: ''
          },
          {
            picture: ''
          }
        ]
      }
    },
    mounted() {
      this.$axios.get('http://landx.top:9002/findInfo').then(
        response => {
          this.find_infos = response.data.data;
          console.log(response.data);
        }
      )
    },
    methods: {
      load() {
        this.count += 2
      }
    }
  }
</script>

<style>
  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .box-card {
    width: 480px;
    background-color: beige;
  }
</style>



