<template>
  <div>
    <div class="buttons">
      <center>
        <el-button round class="findinfo" @click="show_findinfo">失物招领事件</el-button>
        <el-button round class="lostinfo" @click="show_lostinfo">遗失物事件</el-button>
        <el-button round icon="el-icon-search">搜索</el-button>
      </center>
    </div>
    <div id="container"></div>
    <iframe v-bind:src="url" v-bind:hidden="hide_sign" width="400px" height="100%" class="info"
      frameborder="0"></iframe>
  </div>
</template>

<script>
  export default {
    name: 'Map',
    mounted() {
      var map = new BMap.Map("container", {
        minZoom: 16,
        maxZoom: 19,
        poiIcon: false
      }); // 创建Map实例
      map.centerAndZoom(new BMap.Point(116.316702, 39.998516), 17); // 初始化地图,设置中心点坐标和地图级别
      //添加地图类型控件
      map.addControl(new BMap.MapTypeControl({
        mapTypes: [BMAP_NORMAL_MAP]
      }));
      map.setCurrentCity("北京"); // 设置地图显示的城市 此项是必须设置的
      map.enableScrollWheelZoom(true); //开启鼠标滚轮缩放
      map.setMapStyleV2({
        styleId: '16334999f40c2739dc47c4786ac057b7'
      });
      this.$axios.get('http://landx.top:9002/findInfo').then(
        response => {
          var items = response.data.data;
          for (var i in items) {
            var item = items[i];
            var myIcon = new BMap.Icon(require('../assets/' + item.category + '.png'), new BMap.Size(28, 28));
            var pt = new BMap.Point(item.place[0][0], item.place[0][1]);
            var marker = new BMap.Marker(pt, {
              icon: myIcon
            });
            map.addOverlay(marker);
          }
        }
      )
    },
    data() {
      return {
        url: "",
        hide_sign: true,
        show_info_status: 0,
      }
    },
    methods: {
      show_lostinfo() {
        if (this.show_info_status == 1) {
          this.show_info_status = 0;
          this.hide_sign = true;
          this.url = "";
        } else {
          this.show_info_status = 1;
          this.hide_sign = false;
          this.url =window.location.origin+ "/#/lostinfo";
        }
      },
      show_findinfo() {
        if (this.show_info_status == 2) {
          this.show_info_status = 0;
          this.hide_sign = true;
          this.url = "";
        } else {
          this.show_info_status = 2;
          this.hide_sign = false;
          this.url =window.location.origin+"/#/findinfo";
        }
      },
    }
  }
</script>

<style>
  #container {
    position: absolute;
    overflow: hidden;
    width: 99%;
    height: 99%;
    font-family: "微软雅黑";
  }

  .buttons {
    z-index: 1;
    position: fixed;
    margin: auto;
    width: 100%;
  }

  .info {
    z-index: 1;
    position: fixed;
    margin: auto;
    background-color: #F5F5DC;
  }
</style>



