<template>
  <div>
    <div v-for="(lost_info,index) in lost_infos" :key="index">
      <el-card>
        <div slot="header" class="clearfix">
          <div>名称：{{lost_info.name}}</div>
          <div>类型：{{lost_info.category}}</div>
          <div>描述：{{lost_info.discrip}}</div>
          <div>遗失时间：{{lost_info.time}}</div>
          <div>联系方式：{{lost_info.contact_info}}</div>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
  export default{
    name:"lostinfo",
    data(){
      return{
        lost_infos:[
          {id:''},
          {name:''},
          {discrip:''},
          {category:''},
          {path:[
            '',
            ''
          ]},
          {time:''},
          {if_find:''},
          {user_name:''},
          {contact_info:''},
          {picture:''}
        ]
      }
    },
    mounted(){
      this.$axios.get('http://landx.top:9002/lostInfo').then(
      response=>{
        this.lost_infos=response.data.points;
        // alert(response.data.points);
        console.log(response.data);
      }
      )
    }
  }
</script>

<style>
    .clearfix:before,
    .clearfix:after {
      display: table;
      content: "";
    }
    .clearfix:after {
      clear: both
    }
    .box-card {
      width: 600px;
      background-color: beige;
    }
</style>



