<template>
  <div>
    <el-form ref="search_box" :model="form" label-width="80px">
      <el-form-item label="事件类型">
        <el-radio-group v-model="form.type">
          <el-radio label="1">失物招领事件</el-radio>
          <el-radio label="2">遗失物事件</el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="物品名称">
        <el-input v-model="form.name" style="width: 30%;"></el-input>
      </el-form-item>
      <el-form-item label="物品类型">
        <el-select v-model="form.category" placeholder="请选择物品类型">
          <el-option label="生活用品" value="生活"></el-option>
          <el-option label="数码产品" value="数码"></el-option>
          <el-option label="美妆产品" value="美妆"></el-option>
          <el-option label="证件" value="证件"></el-option>
          <el-option label="钥匙" value="钥匙"></el-option>
          <el-option label="其他" value="其他"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="遗失/拾取时间">
        <el-col :span="8">
          <el-date-picker type="date" placeholder="选择日期" v-model="form.date" style="width: 50%;"></el-date-picker>
        </el-col>
        <el-col :span="8">
          <el-time-picker placeholder="选择时间" v-model="form.time" style="width: 50%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item>请绘制搜索范围:</el-form-item>
    </el-form>
    <!-- <div>
      <baidu-map class="map" :center="{lng: 116.316702, lat:39.998516}" :zoom="17" :minZoom="16" :maxZoom="19"
        :scrollWheelZoom="true">
        <bm-polygon :path="path" v-for="path of polygonPath.paths" :key="path.toString()" stroke-color="blue"
          fill-color="red" :fill-opacity="0.8" :stroke-opacity="0.5" :stroke-weight="2" @click="alertpath"
          :editing="true" @lineupdate="updatePolygonPath" />
          <bm-control>
            <button @click="toggle('polygonPath')">{{ polygonPath.editing ? '停止绘制' : '开始绘制' }}</button>
          </bm-control>
      </baidu-map>
    </div> -->
    <iframe src="../../static/test.html" width="1000px" height="600px"></iframe>
  </div>
</template>

<script type="text/javascript" src="http://api.map.baidu.com/library/DrawingManager/1.4/src/DrawingManager_min.js">
</script>
<script>
  export default {
    name: "search",
    data() {
      return {
        publicPath: process.env.BASE_URL,
        form: {
          name: '',
          type: '1',
          date: '',
          time: '',
          category: '',
          path: []
        },
        polygonPath: {
          editing: false,
          paths: [] // 绘制完成后的经纬度，其实是在画的时候动态push的，因为在点击的时候触发了 paintPolygon 函数
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
      }
  }
</script>

<style>
  .map {
    position: absolute;
    overflow: hidden;
    width: 50%;
    height: 50%;
    font-family: "微软雅黑";
  }
</style>



