import Vue from 'vue'
import Router from 'vue-router'
import Login from '../components/Login'
import Map from '../components/Map'
import lostinfo from '../components/lostinfo'
import findinfo from '../components/findinfo'
import search from '../components/search'

Vue.use(Router)

export default new Router({
  routes: [
	  {
		  path:'/',
		  name:'Login',
		  component:Login
	  },
    {
      path:'/map',
      name: Map,
      component:Map
    },
    {
        path:'/lostinfo',
        name:'lostinfo',
        component:lostinfo
    },
    {
      path:'/findinfo',
      name:'findinfo',
      component:findinfo
    },
    {
      path:'/search',
      name:'search',
      component:search
    },
  ]
})



